<template>
  <div class="works_content_page">
    <div v-if="global.mode === 0">
      <div class="first_img_box">
      <img class="first_img" :src="first_img"/>
    </div>
      <div class="intro_part">
        <div class="intro_object">
          <div :class="'content_text_'+global.lang+' object_inline'">{{global.lang_pack.type}}<div
            v-for="(tag, index) of works_content.tag_list"
            :key="tag"
            class="tag"
          >{{ tag }}{{ index === works_content.tag_list.length - 1 ? "" : ", " }}</div>
          </div>
          <div :class="'content_text_'+global.lang+' object_inline'">{{global.lang_pack.client}}{{ works_content.client }}</div>
          <div :class="'content_text_'+global.lang+' object_inline'">{{global.lang_pack.year}}{{ works_content.year }}</div>
        </div>
        <div class="intro_title">
          <div class="intro_title_name">{{ works_content.title }}</div>
          <div class="intro_location">{{ works_content.location }}</div>
        </div>
        <div :class="'content_text_'+global.lang+' intro_content'">{{ works_content.intro }}
        </div>
      </div>

      <div v-if="imgs_layout.length !== 0" class="works_part">
        <div
          v-for="(row, index) of imgs_layout"
          :key="index"
          class="img_row">
          <img
            v-for="(img, index) of row"
            :key="index"
            :src="img.src"
            :style="{width:img.width+'rem', height:img.height+'rem'}"
            class="img">
        </div>
      </div>
    </div>

    <div v-if="global.mode === 1" class="mobile_body">
      <div class="intro_part">
        <div class="intro_object">
          <div :class="'content_text_'+global.lang+' object_inline'">{{global.lang_pack.type}}<div
            v-for="(tag, index) of works_content.tag_list"
            :key="tag"
            class="tag"
          >{{ tag }}{{ index === works_content.tag_list.length - 1 ? "" : ", " }}</div>
          </div>
          <div :class="'content_text_'+global.lang+' object_inline'">{{global.lang_pack.client}}{{ works_content.client }}</div>
          <div :class="'content_text_'+global.lang+' object_inline'">{{global.lang_pack.year}}{{ works_content.year }}</div>
        </div>
        <div class="intro_title">
          <div class="intro_title_name">{{ works_content.title }}</div>
          <div class="intro_location">{{ works_content.location }}</div>
        </div>
        <div :class="'content_text_'+global.lang+' intro_content'">{{ works_content.intro }}
        </div>
      </div>

      <div v-if="imgs_layout.length !== 0" class="works_part">
        <div
          v-for="(row, index) of imgs_layout"
          :key="index"
          class="img_row">
          <img
            v-for="(img, index) of row"
            :key="index"
            :src="img.src"
            class="img">
        </div>
      </div>
    </div>

    <Footer></Footer>
    <Menu></Menu>
    <LangTransfer></LangTransfer>
  </div>
</template>

<style lang="css" scoped>
div.works_content_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  overflow-y: scroll;
  overflow-x: hidden;
}

div.first_img_box{
  width: 100%;
  height: 100%;
  overflow: hidden;
}

img.first_img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 1s;
  filter: grayscale(30%);
}

img.first_img:hover{
  filter: grayscale(0%);
  transform: scale(1.2);
}

div.intro_part {
  margin-left: 11.98%;
  margin-right: 11.98%;
  margin-top: 100px;
  display: grid;
}

div.intro_object {
  grid-row: 1;
  grid-column: 2;
  align-self: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 20px;
}

div.object_inline {
  display: inline-block;
  flex-direction: row;
  white-space: pre-line;
  flex-wrap: wrap;
}

div.tag {
  display: inline-block;
  white-space: pre-wrap;
}

div.intro_title {
  grid-row: 2;
  grid-column: 1;
  width: 500rem
}

div.intro_title_name{
  font-size: 50px;
  white-space: pre-line;
  word-break: break-word;
  word-wrap: break-word;
}

div.intro_location{
  font-size: 50px;
  white-space: pre-line;
  font-family: blk_en;
  word-break: break-word;
  word-wrap: break-word;
}

div.intro_content {
  grid-row: 2;
  grid-column: 2;
}

div.works_part {
  margin-top: 150px;
  position: relative;
  padding-left: 11.98%;
  padding-right: 11.98%;
}

div.img_row {
  margin-top: 30rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

/*手机版样式*/
@media screen and (max-width: 540px) {
  div.intro_part {
    margin-left: unset;
    margin-right: unset;
    margin-top: unset;
    display: grid;
  }

  div.intro_object {
    grid-row: 2;
    grid-column: 1;
    align-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
  }

  div.object_inline {
    display: inline-block;
    flex-direction: row;
    white-space: pre-line;
    flex-wrap: wrap;
  }

  div.tag {
    display: inline-block;
    white-space: pre-wrap;
  }

  div.intro_title {
    grid-row: 1;
    grid-column: 1;
    width: 700rem
  }

  div.intro_title_name{
    font-size: 50px;
    white-space: pre-line;
    word-break: break-word;
    word-wrap: break-word;
    line-height: 1;
  }

  div.intro_location{
    font-size: 50px;
    white-space: pre-line;
    font-family: blk_en;
    word-break: break-word;
    word-wrap: break-word;
    line-height: 1;
  }

  div.intro_content {
    grid-row: 3;
    grid-column: 1;
  }

  div.works_part {
    margin-top: 50px;
    position: relative;
    padding-left: unset;
    padding-right: unset;
  }

  div.img_row {
    display: unset;
    flex-direction: column;
    align-items: center;
    justify-content: unset;
  }

  img.img{
    margin-top: 30rem;
    width: 100%;
  }
}
</style>

<script>
import global from "../../global";
import Menu from "../../../components/menu/Menu";
import LangTransfer from "../../../components/lang_transfer/LangTransfer"
import Footer from "../../../components/footer/Footer"

//export default
export default {
  name: "Works_Content",
  data() {
    return {
      global: global,
      id: this.$route.params.id,
      works_content: require("../../../../static/works/" + this.$route.params.id + "/config.js")["works_content_" + global.lang],
      works_imgs: require("../../../../static/works/" + this.$route.params.id + "/config.js").img_list,
      first_img: "/static/works/" + this.$route.params.id + "/first.png",
      mobile_first_img: "/static/works/" + this.$route.params.id + "/mobile_first.png",
      imgs_layout: [],
    };
  },
  components: {
    Menu,
    Footer,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    this.calc_imgs()
  },
  destroyed() {
  },
  watch: {
    "global.lang":function (i) {
      this.works_content = require("../../../../static/works/" + this.$route.params.id + "/config.js")["works_content_" + global.lang]
    }
  },
  methods: {
    calc_imgs() {
      let that = this
      that.works_imgs.forEach((row) => {
        let row_img = []
        let total = 0
        for (let i = 0; i < row.length; i++) {
          total += row[i].proportion
        }
        let space = 30
        let base_width = (1460 - space * (row.length - 1)) / total
        let min_height = Number.POSITIVE_INFINITY
        for (let i = 0; i < row.length; i++) {
          let img = {}
          img.width = base_width * row[i].proportion
          img.src = "/static/works/" + this.id + "/" + row[i].img
          min_height = Math.min(min_height, img.width / row[i].ratio)
          row_img.push(img)
        }
        row_img.forEach((img) => {
          img.height = min_height
        })
        that.imgs_layout.push(row_img)
      })
    }
  },
};
</script>



